<template>
  <div class="tax-page param-add">
    <h4>参数设置<span>/</span>{{this.param.id?'修改':'添加'}}参数</h4>
    <div class="content">
      <el-form ref="inputForm" :model="param" :rules="rules" label-width="120px">
        <el-form-item label="参数名称" prop="name">
          <el-input v-model="param.name" placeholder="请输入区域名称"></el-input>
          <div class="form-tip">请设置合理的参数名称，设置后不建议修改.</div>
        </el-form-item>
        <el-form-item label="参数类型">
          <div class="types">
            <a v-for="item in pLabels" :key="item.name" :data-label="item.name" :class="item.checked?'cur':''"
               @click.stop="togglelabel">{{item.name}}</a>
          </div>
          <div class="form-tip">参数的类型，可以不设置，类型只是一个分类管理</div>
        </el-form-item>
        <el-form-item label="参数标识" prop="ident">
          <el-input v-model="param.ident" placeholder="请输入参数标识"></el-input>
          <div class="form-tip">参与计算，设置时请注意前缀.</div>
        </el-form-item>
        <el-form-item label="参数数值" prop="config">
          <el-input v-model="param.config" placeholder="请输入参数数值"></el-input>
          <div class="form-tip">可以是一个具体的值或者是一段表达式.</div>
        </el-form-item>
        <el-form-item label="参数单位" prop="unit">
          <el-input v-model="param.unit" placeholder="请输入参数单位"></el-input>
          <div class="form-tip">比例为百分比，金额为元.</div>
        </el-form-item>
        <el-form-item label="参数描述" prop="remarks">
          <el-input v-model="param.remarks" placeholder="请输入参数描述"></el-input>
          <div class="form-tip">简单描述此参数的具体用途.</div>
        </el-form-item>
        <el-form-item label="区域配置">
          <table class="table sample-table table-bordered table-hover">
            <thead>
            <tr>
              <th scope="col" width="120">区域</th>
              <th scope="col">参数值</th>
              <th scope="col" width="120">操作</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(item, index) in param.areas" :key="item.id">
              <td>{{item.areaName}}</td>
              <td>
                <el-input v-if="item.areaId" v-model="item.config" placeholder="请输入内容"></el-input>
              </td>
              <td class="td-ops"><a @click.stop="addArea(index)">添加</a><a @click.stop="delArea(index)">删除</a></td>
            </tr>
            </tbody>
          </table>
          <div class="form-tip">参数标识可以用到上面的表达式中参与计算</div>
        </el-form-item>
        <el-form-item>
          <swak-link class="el-button el-button--primary" @link:click="save">提交</swak-link>
          <swak-link class="el-button" to="/system/param">返回</swak-link>
        </el-form-item>
      </el-form>
      
      <area-select :visible.sync="dialogVisible" @select:area="handleAreaChange"></area-select>
    </div>
  </div>
</template>

<script>
  import Area from '@/api/system/area';
  import {Param} from '@/api/system/param';
  import SwakLink from "../../ui/components/link/link";
  import AreaSelect from "../../ui/components/area/areaSelect";
  
  export default {
    components: {
      AreaSelect,
      SwakLink},
    name: "param-add",
    data() {
      return {
        labels: ['税率', '标准','个税优惠', '社评工资', '五险一金', '年金', '商业保险'],
        areas: [],
        dialogVisible: false,
        loading: true,
        selectedAreas: [],
        addAreaIndex: 0,
        param: {
          id: '',
          versionId: '',
          labels: '',
          name: '',
          ident: '',
          config: '',
          unit: '',
          remarks: '',
          areas: [{}]
        },
        rules: {
          name: [
            {required: true, message: '请输入参数名称', trigger: 'blur'},
          ],
          ident: [
            {required: true, message: '请输入参数标识', trigger: 'blur'},
          ],
          config: [
            {required: true, message: '请输入参数值', trigger: 'blur'},
          ],
          remarks: [
            {required: true, message: '请输入参数描述', trigger: 'blur'},
          ]
        }
      }
    },
    mounted() {
      this.$emit('layout:load', "param")
      var id = this.$route.params.id;
      var versionId = this.$route.query.versionId;
      if (versionId) {
        this.param.versionId = versionId;
      }
      this.load(id)
    },
    computed: {
      pLabels: function () {
        var pLabels = [];
        for (var i = 0; i < this.labels.length; i++) {
          var label = this.labels[i];
          var s = false;
          if (this.param.labels.indexOf("," + label + ",") != -1) {
            s = true;
          }
          pLabels.push({
            name: label,
            checked: s
          });
        }
        return pLabels;
      }
    },
    methods: {
      load: function (id) {
        if (!id) {
          return;
        }
        Param.get(id).then(res => {
          this.param = res.obj;
          if (this.param.areas.length == 0) {
            this.param.areas.push({})
          }
        })
      },
      togglelabel: function (e) {
        var label = ',' + e.target.getAttribute('data-label') + ',';
        if (this.param.labels.indexOf(label) != -1) {
          var labels = this.param.labels.split(label).join(',');
          this.param.labels = labels
        } else {
          var labels = (this.param.labels + label).split(',,').join(',');
          this.param.labels = labels;
        }
      },
      save: function () {
        this.$refs['inputForm'].validate(res => {
          if (res) {
            var data = {
              id: this.param.id,
              versionId: this.param.versionId,
              labels: this.param.labels,
              name: this.param.name,
              ident: this.param.ident,
              config: this.param.config,
              unit: this.param.unit,
              remarks: this.param.remarks,
              'json_areas': JSON.stringify(this.param.areas)
            }
            Param.save(data).then(res => {
              this.$router.replace("/system/param");
              this.$notify.success("保存参数成功!");
            });
          } else {
            this.$notify.error("表单错误");
          }
        });
      },
      addArea: function (index) {
        this.addAreaIndex = index;
        this.dialogVisible = true;
      },
      handleAreaChange: function (area) {
        if (area == 0) {
          this.$notify.warning("请选择一个区域")
          return;
        }

        // add area selected
        if (!this.param.areas[this.addAreaIndex].areaId) {
          this.param.areas.splice(this.addAreaIndex, 1);
        }
        this.param.areas.push({
          areaId: area.id,
          areaName: area.name
        })
      },
      delArea: function (index) {
        this.param.areas.splice(index, 1);
        if (this.param.areas.length == 0) {
          this.param.areas.push({})
        }
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @require "../../ui/common/stylus/variable.styl"
  .param-add {
    .types {
      a {
        display inline-block;
        padding 3px 10px;
        background-color $color-light-grey-s
        margin-right 10px;
        line-height 30px;
      }
      .cur {
        background-color $color-success;
        color $color-white;
      }
    }
  }
</style>