import fetch from '../fetch'

/**
 * Version service
 */
const Version = {
  newest: () => fetch({
    url: '/api/manage/version/newest',
    method: 'get'
  }),
  prev: (id) => fetch({
    url: '/api/manage/version/prev/' + id,
    method: 'get'
  }),
  next: (id) => fetch({
    url: '/api/manage/version/next/' + id,
    method: 'get'
  }),
  build: () => fetch({
    url: '/api/manage/version/build',
    method: 'post'
  }),
};

/**
 * Param service
 */
const Param = {
  get: (id) => fetch({
    url: '/api/manage/param/get/' + id,
    method: 'get'
  }),
  save: (param) => fetch({
    url: '/api/manage/param/save',
    method: 'post',
    data: param
  }),
  sort: (param) => fetch({
    url: '/api/manage/param/sort',
    method: 'post',
    data: {json_params: param}
  }),
  custom: (param) => fetch({
    url: '/api/manage/param/custom',
    method: 'post',
    data: {id: param.id, custom: param.custom}
  }),
  list: (version) => fetch({
    url: '/api/manage/param/list/' + version,
    method: 'get',
  }),
  areas: (id) => fetch({
    url: '/api/manage/param/areas/' + id,
    method: 'get',
  })
}

/**
 * export service
 */
export {
  Version,
  Param
}